<template>
  <v-card height="auto">
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <p class="text-h4 white--text">Change Password</p>
        </v-col>
        <v-col class="text-h6 white--text" cols="12" md="3">Old Password:</v-col>
        <v-col cols="12" md="7">
          <v-text-field
            v-model="oldPas"
            type="password"
            class="ml-auto"
            dark
            outlined
            dense
            clearable
            label="Please enter the old password"
          ></v-text-field>
        </v-col>
        <v-col class="text-h6 white--text" cols="12" md="3">Setting New Password:</v-col>
        <v-col cols="12" md="7">
          <v-text-field
            v-model="password"
            :rules="rules.pwd"
            type="password"
            class="ml-auto"
            dark
            outlined
            dense
            clearable
            label="Please enter the new password"
          ></v-text-field>
        </v-col>
        <v-col class="text-h6 white--text" cols="12" md="3">Repeat New Password:</v-col>
        <v-col cols="12" md="7">
          <v-text-field
            v-model="repassword"
            :rules="[repwd]"
            type="password"
            class="ml-auto"
            dark
            outlined
            dense
            required
            clearable
            label="Please enter the new password"
          ></v-text-field>
        </v-col>
        <v-col cols="12" class="text-end">
          <v-btn color="primary" depressed height="40" @click="passwordSend()"
            >Send</v-btn
          >
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { changePwd } from "../../api";

export default {
  data: () => ({
    oldPas: "",
    password: "",
    repassword: "",
    rules: {
      pwd: [
        (v) => !!v || "Please enter the password",
        (v) =>
          /^[a-zA-Z0-9]+$/.test(v) ||
          "Please enter uppercase and lowercase letters or numbers",
        (v) =>
          (v && v.length <= 12) ||
          "Password length must be less than 12 characters",
        (v) =>
          (v && v.length >= 8) ||
          "Password length must be more than 8 characters",
      ],
    },
  }),
  methods: {
    //再次確認密碼
    repwd(val) {
      if (val !== this.password) {
        return "Inconsistent passwords";
      }
      return true;
    },

    //密碼變更送出
    passwordSend() {
      this.sendPasswordSendApi();
    },

    //密碼變更api
    sendPasswordSendApi() {
      var params = new URLSearchParams();
      params.append("PassWod_old", this.oldPas);
      params.append("PassWod", this.password);

      changePwd(params)
        .then((result) => {
          if (result.data.success == true) {
            alert("Password has been changed");
            location.reload();
          } else {
            alert(result.data.error_msg);
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
};
</script>